<template>
  <div class="header-wrapper">
    <div class="currentcity">{{city}}</div>
    <span class="wendu">
      <span v-if ="flag" class='placehold'>--</span>
      <span class="wendunumber">{{temper}}</span>℃
      <p class="notice">{{notice}}</p>
    </span>
    <div class="weatherList">
      <div v-for ='item in forecast' >
        {{item.date}}
        {{item.high}}
        {{item.low}}
        {{item.type}}
        {{item.fengxiang}}
      </div>
    </div>
  </div>
</template>

<script>
//import store from '../../../store'
import {mapState,mapMutations,mapActions} from 'vuex'
import axios from 'axios'
export default {
  name: 'HomeHeader',
  props:{
  	list:String
  },
  data() {
  		return{
        temper:null,
        notice:null,
        forecast:null
  		}
  },
  methods:{
    getData(){
      axios.get('api?city='+this.city).then(this.sayData)
    },
    sayData(res){
      var data = res.data.data;
      console.log(data)
      this.temper = data.wendu
      this.notice = data.ganmao
      this.forecast = data.forecast
    },
    ...mapMutations(['changeCity','changeNumber']),
    ...mapActions(['increment'])
  }, 
  computed:{ 
    flag(){
      return this.temper? false : true;
    },
    ...mapState({
      number:'number',
      city:'city'
    })
  },
  mounted(){
    this.getData()
  }
}
</script>
<style scoped lang="stylus">
.wendu
  width:100px;
  height:100px;
  font-size:40px;
  font-weight:bold;
.currentcity
  width:50px;
  height:50px;
  font-size:20px;
.placehold&.wendunumber
  display:inline-block;
  width:50px;
  height:50px;
.notice{
  font-size:10px;
  font-weight:normal;
}
</style>


